<script>
import mapboxgl from 'mapbox-gl';
import U from 'mapbox-gl-utils';
import axios from 'axios';
export default {
    async mounted() {
        mapboxgl.accessToken = 'pk.eyJ1Ijoic3RldmFnZSIsImEiOiJGcW03aExzIn0.QUkUmTGIO3gGt83HiRIjQw';
        const style = {
