            container: 'map',
            center: [145, -37.8],
            zoom: 12,
            style//: 'mapbox://styles/mapbox/light-v9',
        });
        U.init(map, mapboxgl);
        window.map = map;
        window.Map = this;
        map.U.onLoad(() => {
            map.U.addGeoJSON('tiles', require('./tiles.json'))
            // map.U.addSymbol('tiles-flag', 'tiles', {
                //     iconImage: 'aus'
            // });
            map.U.addLayer('bg', 'background', 
                { backgroundColor: '#555', type: 'background' });
            map.addSource('points', { type: 'geojson',  data: `${window.location}flagtest.geojson` })
            map.U.addLine('tiles-line', 'points', {
                lineColor: 'blue'
            })
            map.U.addSymbol('points-flag', 'points', {
                iconImage: '{country}',
                iconAllowOverlap: true
            });
            map.U.hoverPopup('points-flag', f => `${f.properties.country}`)
        });
        
        // map.U.hoverPointer('place-suburb');
        // map.on('click', 'place-suburb', e => {
        //     console.log(e);
